<template>
  <div v-if="dataLoaded" class="container mt-10 px-4">
    <!-- No Data -->
    <div v-if="data.length === 0" class="w-full flex flex-col items-center">
      <h1 class="text-2xl">Looks empty here...</h1>
      <router-link class="mt-6 py-2 px-6 rounded-sm  text-sm
            text-white bg-at-blue duration-200 border-solid
            border-2 border-transparent hover:border-at-blue hover:bg-white
            hover:text-at-blue" :to="{ name: 'Create' }">
        Create Workout
      </router-link>
    </div>

    <!-- Data -->
    <div v-else class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
      <router-link class="flex flex-col items-center bg-light-grey p-8 shadow-md cursor-pointer"
                   :to="{ name: 'View-Workout', params: { workoutId: workout.id } }"
                    v-for="(workout, index) in data"
                   :key="index">
        <!-- Cardio Img -->
        <img v-if="workout.workoutType === 'cardio'" src="@/assets/images/running-blue.svg" class="h-14 w-auto" alt=""/>

        <!-- Strength Training -->
        <img v-else src="@/assets/images/dumbell-blue.svg" class="h-14 w-auto" alt=""/>

        <p class="mt-6 py-1 px-3 text-xs text-white bg-at-blue shadow-md rounded-lg">
          {{ workout.workoutType }}
        </p>

        <h1 class="mt-4 mb-2 text-center text-xl text-at-blue">
          {{ workout.workoutName }}
        </h1>

        <p class="mt-2 py-1 px-3 text-xs text-white bg-at-blue shadow-md rounded-lg">
          Created by {{ workout.username }}
        </p>
      </router-link>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { supabase } from "@/supabase/init";

export default {
  name: "Home",
  components: {},
  setup() {
    // Create data / vars
    const data = ref([]);
    const dataLoaded = ref(null);

    // Get data
    const getData = async () => {
      try {
        const { data: workouts, error } = await supabase.from("workouts").select("*");
        if (error) throw error;
        data.value = workouts;
        dataLoaded.value = true;
      } catch (error) {
        console.warn(error.message);
      }
    };
    // Run data function
    getData();

    return { data, dataLoaded };
  },
};
</script>
